
#state-code{text-align:center;height:100%;background-color:#000;overflow:auto
}
#state-code .phi-input{display:block;color:#fff
}
#state-code form{width:270px;margin:auto;margin-top:76px;background:url("../assets/phidias-label.png") no-repeat center top;padding-top:112px
}
#state-code form footer{margin:48px 0
}
#state-code #dialog{text-align:left;padding:12px 24px
}
#state-code #dialog h2{margin:0;margin-bottom:1em
