
#state-login{padding:6px;padding-top:5%;text-align:center
}
#state-login .phi-card{margin:auto;width:100%;max-width:600px;padding:12px
}
#state-login .phi-card img{max-width:100%;margin:24px auto;background-color:#2196F3
}
#state-login .phi-input{display:block;margin-bottom:32px
}
#state-login .phi-button{display:block;width:100%;margin-bottom:16px
}
#state-login .phi-button.google{background-color:#eee;color:#333
}
#state-login .reset{display:block;margin:auto;width:100%;max-width:600px;margin-top:15px;padding:12px;color:#555;text-transform:uppercase;text-decoration:none
}



