
.slidetab-enter-active,.slidetab-leave-active{position:absolute;top:16px;left:16px;right:16px;transition:transform .42s,opacity .42s
}
.slidetab-enter-active ons-fab,.slidetab-leave-active ons-fab{display:none
}
.slidetab-enter,.slidetab-leave-active{opacity:0
}
.moving-left .slidetab-enter{transform:translate3d(210px, 0, 0)
}
.moving-left .slidetab-leave-active{transform:translate3d(-210px, 0, 0)
}
.moving-right .slidetab-enter{transform:translate3d(-210px, 0, 0)
}
.moving-right .slidetab-leave-active{transform:translate3d(210px, 0, 0)
}



